/* styles/TabsSection.module.css */
.tabHeader {
  display: flex;
  border-bottom: 2px solid #eee;
  width: 100%;
  flex-wrap: wrap;
}

.tabButton {
  padding: 1rem 2rem;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  color: #ffff;
  transition: all 0.3s ease;
}

.tabButton:hover {
  color: #fa198c;
}

.tabButton.active {
  color: #fa198c;
  border-bottom: 2px solid #fa198c;
  margin-bottom: -2px;
}

.tabContent {
  margin-top: 30px;
}

.inner-wrap h1 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.inner-wrap p {
  font-size: 1.1rem;
  margin-bottom: 1.5rem;
}

.bg-wrap {
  width: 100%;
  max-height: 400px;
  overflow: hidden;
}

.mobileDropdown {
  display: none;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .tabHeader {
    display: none;
  }

  .mobileDropdown {
    display: block;
    position: relative;
  }

  .dropdownToggle {
    width: 100%;
    padding: 10px 20px;
    background: #fff;
    border: 1px solid #eee;
    font-size: 22px;
    font-weight: 900;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #000;
  }

  .arrow {
    width: 12px;
    height: 12px;
    border-right: 2px solid #000;
    border-bottom: 2px solid #000;
    transform: rotate(45deg);
    transition: transform 0.2s linear, -webkit-transform 0.2s linear,
      -o-transform 0.2s linear;
  }

  .mobileDropdown .dropdownToggle:has(+ .dropdownMenu) .arrow {
    transform: rotate(-135deg);
  }

  .dropdownMenu {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    background: #fff;
    box-shadow: 0px 4px 25px 0px rgba(91, 91, 91, 0.15);
    border-top: none;
    z-index: 1000;
  }

  .dropdownItem {
    width: 100%;
    padding: 10px 20px;
    background: none;
    border: none;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
    text-align: left;
    color: #000;
  }

  .dropdownItem:hover {
    background: #f5f5f5;
  }

  .dropdownItem.active {
    color: #fa198c;
    background: #fafafa;
  }

  .tabContent {
    padding: 1rem;
  }

  .inner-wrap h1 {
    font-size: 1.5rem;
  }

  .inner-wrap p {
    font-size: 1rem;
  }
}
